import {request} from '@/api/reques.js'
export default{
	props:{
		name:{
			type:String,
			default:''
		},
		num:{
			type:String,
			default:''
		},
		idJson1:{
			type:Object,
		},
		imgSrc1:{
			type:String,
		},
		imgSrc2:{
			type:String,
		},
		show:{
			type:Boolean,
			default:true,
		}
	},
	data(){
		return{
			
		}
	},
	methods:{
		clickDetermine(){
			request('/member/addMemberIdCard',{
				token: uni.getStorageSync('token'),
				face_img_url:this.imgSrc1,
				back_img_url:this.imgSrc2,
				real_name:this.name,
				id_num:this.num,
				id_json:JSON.stringify(this.idJson1)
			},'POST')
			.then((res)=>{
				if(res.data.data === 1){
					uni.setStorageSync('shenfen_key',res.data.data);
					uni.showToast({
					    title: '实名认证成功',
						icon:'none',
					    duration: 3000,
					});
					setTimeout(()=>{
						uni.navigateBack({
						    delta: 1
						});
					},2000)
				}
			})
		}
	}
}
